<template>
  <div class="m_collapse">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <van-row class="m_title">
          <van-col span="8" offset="8">买价(bp)</van-col>
          <van-col span="8">卖价(bp)</van-col>
        </van-row>

        <van-collapse v-model="activeNames">
          <van-collapse-item :title="item.title" :name="index" v-for="(item,index) in list">
            <van-row class="m_item" v-for="item2 in item.value">
              <van-col span="8">0.5Y</van-col>
              <van-col span="8">32.00</van-col>
              <van-col span="8">32.00</van-col>
            </van-row>
          </van-collapse-item>
        </van-collapse>

      </van-pull-refresh>
    </section>

  </div>
</template>

<script>
  export default {
    name: "creditDefaultSwap",
    data() {
      return {
        title: '信用违约互换',
        isLoading: false,
        activeNames: [0, 1, 2, 3, 4],
        list: [
          {title: '超AAA', value: [1,2,3,4]},
          {title: '普通AAA', value: [1,2,3,4]},
          {title: 'AA+', value: [1]},
          {title: 'AA', value: [1,2,3,4]},
        ]
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      }
    }
  }
</script>

<style scoped>

</style>



